* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Bahnschrift', 'Gill Sans MT', sans-serif;
  text-decoration: none;
}

body {
  background-color:rgb(240, 231, 231);
}

.container{
  max-width: 1300px;
  padding: 0 80px;
  margin: auto;
}

/*Header styling*/

header {
  padding: 10px;
  background-color: black;
  color: #ffffff;
  width: 100%;
}

header h1 {
  display: inline-block;
  font-size: 36px;
  font-weight: 400;
  margin-left: 40px;
}

header h1 .titlecolor {
  color: #008fb3;
}

/* To Top Button */
#myBtn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: #008fb3;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 4px;
}

#myBtn:hover {
  background-color: #555;
}

/*Navbar styling*/

nav {
  padding: 10px;
  float: right;
  font-size: 18px;
  font-weight: 350;
  background-color: black;
}

nav ul li a{
  color: white;
  position: relative;
}

nav ul li a::after{
  content: "";
  width: 0;
  height: 3px;
  background: #008fb3;
  position: absolute;
  left: 0;
  bottom: -6px;
  transition: 0.5s;
}

nav ul li a:hover {
  color: #008fb3;
}

nav ul li a:hover::after{
  width: 100%;
}

nav ul li{
  list-style: none;
  display: inline-block;
  margin-left: 30px;
}

nav .container{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/*Home section styling*/

.home{
  display:flex;
  flex-wrap: wrap;
  height: 800px;
  min-height: 250px;
  width: 100%;
  margin-bottom: 80px;
  background: url("../src/assets/images/background.jpg") ;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

.home .container{
  margin: auto;
}

.home .welcome h2{
  font-size: 50px;
  font-weight: 350;
  color: white;
  text-align: center;
}

.home h2 .titlecolor{
  color: #008fb3
}

/*About section styling*/

.about h3{
  font-size: 42px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 60px;
  position: relative;

}

.about h3::before{
  content: "";
  width: 178px;
  height: 3px;
  background: #008fb3;
  position: absolute;
  bottom: -3px;
}

.about .about-content{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.about .about-content .left{
  width: 45%;
}

.about .about-content .left img{
  height: 500px;
  width: 350px;
  object-fit: cover;
  border-radius: 40px;
}

.about .about-content .right{
  width: 55%;
}

.about .about-content .right p{
  font-size: 20px;
  text-align: justify;
}

/*Work article styling*/

article h4{
  font-size: 42px;
  font-weight: 500;
  text-align: center;
  margin-top: 100px;
  margin-bottom: 60px;
  position: relative;
}

article h4::before{
  content: "";
  width: 103px;
  height: 3px;
  background: #008fb3;
  position: absolute;
  bottom: -3px;
}

article .work-content{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 80px;
}

.work-content .main-work img{
  display:flex;
  height: 550px;
  min-height: 250px;
  width: 100%;
  object-fit: cover;
  justify-content: space-between;

}

.work-content .coming-soon{
  width: 45%;
}
.work-content .coming-soon img{
  height: 300px;
  width: 550px;
  object-fit: cover;
}

.work-content .more-coming-soon img{
  height: 300px;
  width: 550px;
  object-fit: cover;
}

.work-content h5 {
  margin-left: auto;
  text-align: left;
  font-size: 20px;
  font-weight: 400;
  color: #008fb3;
}


.work-content figure figcaption {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.work-content .main-work{
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
  transition: 0.5s;
}

.main-work:hover{
  opacity: 0.5;
  transition: 0.5s;
}

.work-content .coming-soon{
  position: relative;
}

.coming-soon:hover{
  opacity: 0.5;
  transition: 0.5s;
}

.work-content .more-coming-soon{
  position: relative;
}

.more-coming-soon:hover{
  opacity: 0.5;
  transition: 0.5s;
}

/*Contact Form Styling*/

input[type=text], select, textarea {
  width: 100%; 
  padding: 12px; 
  border: 1px solid #ccc; 
  border-radius: 4px; 
  box-sizing: border-box; 
  margin-top: 6px; 
  margin-bottom: 16px; 
  resize: vertical 
}

input[type=submit] {
  background-color: #008fb3;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: #ccc;
}

/*
.form-container {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 500px;
  padding-right: 500px;
}
*/

.container p {
  text-align: right;
  display: inline-block;
  padding: 10px;
}

.container textarea {
  height: 200px;
}

#submit-button {
  float: right;
}

.contact-header {
  font-size: 42px;
  font-weight: 500;
  text-align: center;
  margin-top: 100px;
  margin-bottom: 10px;
  position: relative;
}

.contact-header::before{
  content: "";
  width: 240px;
  height: 3px;
  background: #008fb3;
  position: absolute;
  bottom: -3px;
}

form {
  padding: 20px;
}

/* The Modal */
.modal {
  display: none; 
  position: fixed; 
  z-index: 1; 
  padding-top: 150px; 
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; 
  background-color: rgba(0,0,0,0.4); 
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.modal-content p {
  text-align: center;
}

/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

/*Contact Footer styling*/

footer{
  margin-bottom: 15px;
}

footer h6{
  font-size: 42px;
  font-weight: 500;
  text-align: center;
  margin-top: 100px;
  margin-bottom: 60px;
  position: relative;
}

footer h6::before{
  content: "";
  width: 210px;
  height: 3px;
  background: #008fb3;
  position: absolute;
  bottom: -3px;
}

footer ul li{
  list-style: none;
  display: inline-block;
  width: 33%;
  text-align: center;
  font-size: 24px;
  margin-bottom: 25px;
}

footer ul li a{
  color: black;
  position: relative;
}

footer ul li a::after{
  content: "";
  width: 0;
  height: 3px;
  background: #008fb3;
  position: absolute;
  left: 0;
  bottom: -6px;
  transition: 0.5s;
}

footer ul li a:hover {
  color: #008fb3;
}

footer ul li a:hover::after{
  width: 100%;
}

/* Media Query*/

@media screen and (max-width: 1280px){


  .coming-soon{
      justify-content: space-between;
      flex-direction: column;
      flex: 100%;
      margin: 0 auto 20px;
  }

  .more-coming-soon{
      flex: 100%;
  }
  .work-content .main-work img{
      height: 250px;
      min-height: 450px;
      transition: 0.5s;
  }
  .work-content .coming-soon img{
      height: 200px;
      min-width: 250px;
      width: 100%;
      transition: 0.5s;
  }
  .work-content .more-coming-soon img{
      height: 200px;
      min-width: 250px;
      width: 100%;
      transition: 0.5s;
  }
}

@media screen and (max-width: 1030px){
  footer ul li{
      width: 30%;
      font-size: 20px;
      transition: 0.5s;
  }

  .about .about-content .left img{
      height: 400px;
      width: 250px;
      transition: 0.5s;
  }

  .about .about-content .right p{
      font-size: 18px;
      transition: 0.5s;
  }
}

@media screen and (max-width: 832px){

  nav {
      float:none     
  }

  .about .about-content .left img{
      height: 300px;
      width: 200px;
      transition: 0.5s;
  }

  .about .about-content .right p{
      font-size: 16px;
      transition: 0.5s;
  }

}

@media screen and (max-width: 685px){
  .work-content .main-work img{
      height: 200px;
      min-height: 250px;
      transition: 0.5s;
  }
  .about .about-content .right p{
      font-size: 14px;
      transition: 0.5s;
  }

  .about .about-content .column{
      width: 100%;
      transition: 0.5s;
  }
  .about .about-content .left{
      display: flex;
      justify-content: center;
      margin-bottom: 40px;
      transition: 0.5s;
  
  }
  .about .about-content .right{
      flex: 100%;
      transition: 0.5s;
  }

}

@media screen and (max-width: 400px){
  header {
      text-align: center;
  }
  nav {
      font-size: 14px;
  }
  .home{
      height: 600px;
      transition: 0.5s;
  }
  .home .welcome h2{
      font-size: 34px;
      transition: 0.5s;
  }
  .about h3{
      font-size: 32px;
      transition: 0.5s;
  }
  .about h3::before{
      width: 135px;
      transition: 0.5s;
  }

  .about .about-content .left img{
      height: 250px;
      width: 150px;
      transition: 0.5s;
  }

  .about .about-content .right p{
      font-size: 12px;
      transition: 0.5s;
  }

  article h4{
      font-size: 32px;
      transition: 0.5s;
  }
  
  article h4::before{
      width: 80px;
      transition: 0.5s;
  }
  .work-content .main-work img{
      height: 120px;
      min-height: 150px;
      transition: 0.5s;
  }
  .work-content .coming-soon img{
      height: 120px;
      min-width: 150px;
      width: 100%;
      transition: 0.5s;
  }
  .work-content .more-coming-soon img{
      height: 120px;
      min-width: 150px;
      width: 100%;
      transition: 0.5s;
  }
  footer h6{
      font-size: 24px;
      transition: 0.5s;
  }
  
  footer h6::before{
      width: 120px;
      transition: 0.5s;
  }

  footer ul li{
      width: 28%;
      font-size: 14px;
      transition: 0.5s;
  }
}